.author_profile,
.custom-notifiction {
    .progress-wrapper {
        position: relative;
        width: 40px;
        height: 40px;
        text-align: center;
        font-family: sans-serif;

        .progress-circle {
            position: relative;
            margin: auto;
            width: 40px;
            height: 40px;

            >.rounded-circle {
                // border: 3px solid #fff;
                border: 3px solid var(--bs-light);
                top: 3px;
                left: 3px;
            }

            .progress-ring {
                position: absolute;
                top: 0;
                left: 0;
                transform: rotate(110deg);
                width: 40px;
                height: 40px;
                z-index: 1;

                circle {
                    fill: none;
                    stroke-width: 7;
                }

                .progress-bg {
                    stroke: #e6e6e6;
                }

                .progress-bar {
                    stroke: #6AC259;
                    stroke-dasharray: 339.292;
                    stroke-dashoffset: 0;
                    transition: stroke-dashoffset 0.5s ease;
                }
            }
        }

        .progress-label {
            position: absolute;
            bottom: 0;
            font-size: 9px;
            font-weight: bold;
            color: #6AC259;
            z-index: 2;
            box-shadow: 0px 1px 4px 0px #878FBA40;
            // background-color: rgba(255, 255, 255, 1);
            background-color: var(--bs-light);
            padding: 1px;
            border-radius: 5px;
            width: 30px;
            height: 12px;
            margin: 0 auto;
            left: 0;
            right: 0;
        }
    }
}

